import { getCategoryList } from '@/api/biunique'
import BiuniqueCategoryList from '@/components/biunique/BiuniqueCategoryList'
export default {
  components: {
    BiuniqueCategoryList
  },
  async asyncData(context) {
    const { data }  = await getCategoryList({parentId:context.route.params.id, type:3})
    if(data.code === 0) {
      return {list:data.data.entries}
    }
  },
  data() {
    return{
      list:[]
    }
  },
  head () {
    return {
      title: '科研背景提升'
    }
  }
}
